class ToolBox {
  constructor(container, settings, identifier) {
    this.itemTemplate = ({thumb, name}) => `<div class='tools-item'><div><img src='${thumb}'>${name}</div></div>`;
    this.categories = [];
    this.$container = container;
    const add = (widgetConfigs) => this.categories.push(widgetConfigs);
    settings.map((_, i) => add(settings[i]));
    this.categories.map((category) => $(this.itemTemplate(category)).data(identifier, category).draggable({cursorAt: {top: -5, left: -5}, helper: "clone"}).appendTo(this.$container));
    return $(container);
  }
}
export default ToolBox;
